/*筛选内容小三角的css实现*/
.triangle {
  width: 0;
  height: 0;
  display: inline-block;
  border-width: 0.12rem 0.12rem 0 0.12rem;
  border-color: #fff transparent transparent transparent;
  border-style: solid;
  vertical-align: middle;
}
/*箭头向上*/
.triangle_up {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}
/*箭头向左*/
.triangle_left {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}
/*箭头向右*/
.triangle_right {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}
